
.info {
  background: #f3f4f5;
  padding: 60px 0 90px;
  &__list {
    flex: 0 1 55%;
    max-width: 55%;
    &-el {
      display: flex;
      margin-bottom: 40px;
      &:last-child {
        margin-bottom: 0; } }

    &-icon {
      flex: 0 0 60px;
      max-width: 60px;
      .ico {
        overflow: visible;
        &-fingerprint {
          font-size: 37px; }
        &-clock {
          font-size: 33px; }
        &-drop {
          font-size: 45px; } } }

    &-content {
      flex: 0 1 100%;
      max-width: 100%;
      .heading {
        margin-top: 10px;
        margin-bottom: 10px; }
      p {
        color: $fontSecondary;
        margin: 0;
        & + p {
          margin-top: 1em; } } } }

  &__card {
    padding: 0 15px;
    flex: 0 1 45%;
    max-width: 45%;
    margin-bottom: -7%;
    &-fly {
      margin-right: -37%;
      text-align: left;
      img {
        width: 100%; } } } }

.info-for {
  display: flex;
  margin-left: -15px;
  margin-right: -15px;
  margin-top: 45px;
  &-heading {
    font-size: 22px; }
  &__left {
    flex: 0 1 55%;
    max-width: 55%;
    min-width: 1px;
    min-height: 0;
    display: flex;
    padding: 0 15px; }
  &__right {
    flex: 0 1 45%;
    max-width: 45%;
    min-width: 1px;
    min-height: 0;
    display: flex;
    padding: 0 15px; }

  &__text-el {
    flex: 1 1 auto;
    max-width: 100%;
    min-width: 1px;
    min-height: 0;
    padding-right: 15px;
    &:last-child {
      padding-right: 0; } }
  &__text-icon {
    .ico {
      font-size: 32px;
      &-cart {
        transform: translateY(-3px); } } }

  &__text-content {
    margin-top: 15px;
    font-size: 16px;
    color: $fontSecondary; }

  &__icon-el {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    max-width: 100%;
    min-width: 1px;
    min-height: 0;
    padding-top: 40px;
    padding-right: 15px;
    &:last-child {
      padding-right: 0; } } }


@include r($desktop) {
  .info {
    padding: 40px 0 70px 0;
    &__list {
      &-content {
        p {
          font-size: 15px; } } }
    &__card {
      &-fly {
        margin-right: -30%; } } }

  .info-for-heading {
    font-size: 18px; }

  .info-for {
    &__text-content {
      font-size: 14px; } } }

@include r($tablet) {
  .info {
    padding: 15px 0 40px 0;
    &__list {
      flex: 0 0 auto;
      width: 100%;
      max-width: 100%;
      &-el {
        margin-bottom: 20px; }
      &-icon {
        flex: 0 0 50px;
        max-width: 50px;
        .ico {
          overflow: visible;
          &-fingerprint {
            font-size: 34px; }
          &-clock {
            font-size: 31px; }
          &-drop {
            font-size: 42px; } } }
      &-content {
        .heading {
          margin-top: 7px;
          margin-bottom: 7px; }
        p {
          font-size: 13px; } } }
    &__card {
      flex: 0 0 auto;
      width: 100%;
      max-width: 400px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 0;
      margin-top: -45px;
      &-fly {
        margin-right: 0; } } }

  .info-for-heading {
    font-size: 13px;
    max-width: 300px; }

  .info-for {
    max-width: 400px;
    margin-right: auto;
    margin-top: 15px;
    flex-direction: column-reverse;
    &__left {
      flex: 0 0 auto;
      max-width: 100%; }
    &__right {
      flex: 0 0 auto;
      max-width: 100%;
      margin-bottom: 15px; }

    &__text {
      &-el {
        flex: 0 1 33.3%;
        max-width: 33.3%;
        padding-right: 5px; }
      &-icon {
        .ico {
          font-size: 28px;
          &-cart {
            transform: translateY(-2px); } } } }
    &__text-content {
      margin-top: 10px;
      font-size: 10px; }

    &__icon-el {
      padding-top: 0;
      flex: 0 1 33.3%;
      max-width: 33.3%;
      // display: block
      overflow: visible;
      padding-right: 0;
      .icon {
        flex: 0 0 auto;
        transform-origin: left center;
        transform: scale(.55); } } } }
